/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Overpass", sans-serif;
  background-color: hsl(216deg, 12%, 8%);
  padding: 30vh 2vh;
}
body main, body section {
  border-radius: 2rem;
}

.show {
  display: block;
  transition: all 0.8s ease-out;
}

.hidden {
  display: none;
}

.contenedor {
  background-color: #1f262e;
  width: 95%;
  max-width: 180rem;
  padding: 2rem;
  margin: 0 auto;
}
@media (min-width: 376px) {
  .contenedor {
    width: 35vh;
  }
}

h1, h2, h3, p {
  margin: 0;
}

p {
  font-size: 1.5rem;
}

.mainTitle {
  margin: 0 auto;
  font-size: 2.2rem;
  text-align: left;
  color: hsl(0deg, 0%, 100%);
  padding: 1rem 0;
}

.mainParagraph {
  color: hsl(217deg, 12%, 63%);
  margin-bottom: 2rem;
}

a {
  text-decoration: none;
}

