.rating {
  padding: 2rem;
}
.rating .starIcon {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  height: 2rem;
  width: 2rem;
  border-radius: 100% 100%;
  background-color: #2b3540;
}
.rating .starIcon img {
  margin: 0 auto;
  width: 2rem;
}

.ratingForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.ratingForm .ratingButtons {
  display: flex;
  gap: 2rem;
}
.ratingForm .ratingButtons .button {
  position: fixed;
  width: 0;
  opacity: 0;
  background-color: #2b3540;
}
.ratingForm .ratingButtons .button:checked + label {
  background-color: hsl(25deg, 97%, 53%);
  color: hsl(0deg, 0%, 100%);
}
.ratingForm .ratingButtons .rate-label {
  font-size: 1.8rem;
  display: inline-grid;
  place-content: center;
  background-color: #2b3540;
  border-radius: 50%;
  height: 4rem;
  width: 4rem;
  color: hsl(217deg, 12%, 63%);
}
@media (min-width: 376px) {
  .ratingForm .ratingButtons .rate-label {
    gap: 1rem;
    height: 4.4rem;
    width: 4.4rem;
  }
}
.ratingForm .ratingButtons .rate-label:hover {
  cursor: pointer;
  background-color: #8092a8;
  color: hsl(0deg, 0%, 100%);
}
.ratingForm .ratingSubmit {
  border: none;
  border-radius: 3rem;
  width: 95%;
  padding: 1.6rem;
  font-size: 1.5rem;
  font-weight: 400;
  margin: 3rem 0 1rem 0;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.2rem;
  background-color: hsl(25deg, 97%, 53%);
  color: hsl(0deg, 0%, 100%);
}
.ratingForm .ratingSubmit:hover {
  cursor: pointer;
  background-color: hsl(0deg, 0%, 100%);
  color: hsl(25deg, 97%, 53%);
}

.submited {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.submited.hidden {
  display: none;
}
.submited img {
  padding: 1rem;
  width: 50%;
}
.submited .submitTitle {
  border-radius: 2rem;
  background-color: #2b3540;
  height: 3rem;
  width: 80%;
  color: hsl(25deg, 97%, 53%);
  text-align: center;
  padding: 0.8rem;
  margin: 1rem 0 1.6rem 0;
}
@media (min-width: 376px) {
  .submited .submitTitle {
    width: 75%;
  }
}
.submited .mainTitle {
  font-size: 2.6rem;
  margin-bottom: 0.6rem;
}
.submited .mainParagraph {
  text-align: center;
  line-height: 1.5;
}